<template>
    <div class="auth-page">
        <div class="background"></div>
        <div class="backdrop"></div>

        <div class="container">
            <div class="formulario">
                <!-- HEADER -->
                <div class="header">
                    <div class="logo">
                        <div class="logo-area">
                            <span class="big">ENDURANCE</span>
                        </div>
                    </div>
                </div>
                <!-- FORMULARIO -->
                <form method="post" action="" class="form">
                    <div class="input">
                        <i class="fa fa-user icone"></i>
                        <input type="user" name="user" placeholder="Usuario" v-model="objItem.username">
                    </div>
                    <div class="input">
                        <i class="fa fa-lock icone"></i>
                        <input :type="(viewPass) ? 'text' : 'password'" id="login-password" name="password"
                            placeholder="Senha" v-model="objItem.password">
                        <i id="show-password" class="fa fa-eye icone" v-show="!viewPass" @click="viewPass = true"></i>
                        <i id="show-password" class="fa fa-eye-slash icone" v-show="viewPass"
                            @click="viewPass = false"></i>
                    </div>
                    <div class="remember">
                        <label for="rememberme">
                            Lembre-me
                            <input type="checkbox" name="rememberme" id="rememberme" v-model="objItem.rememberme">
                            <span class="checkmark"></span>
                        </label>
                    </div>

                    <p class="msg-error" v-show="msgError != null">* {{ msgError }}</p>

                    <p class="w-100 text-center" v-show="loading">Aguarde...</p>
                    <input value="Login" class="btn btn-login" @click="sign()" v-show="!loading">
                    <a href="#/signup" class="btn btn-registrar">Criar Conta</a>
                </form>
            </div>
            <p class="attrib">&copy; 2023 Filipe Daineze</p>
        </div>
    </div>
</template>
<style lang="scss">

</style>
<script>
import router from 'vue-router';
import { token, setToken } from '@/js/variables';
import { signIn } from '@/js/auth';

export default {
    components: {
    },
    data() {
        return {
            token: token,
            objItem: {},
            viewPass: false,
            msgError: null,
            loading: false
        };
    },
    methods: {
        setToken,
        signIn,

        async sign() {
            this.loading = true;
            let payload = JSON.parse(JSON.stringify(this.objItem));

            if (payload.username == null || payload.password == null) {
                this.msgError = 'Usuário ou senha incorretos';
                this.loading = false;
            } else {
                const signin = await this.signIn(payload);

                if (signin.status == 'error') {
                    this.msgError = 'Usuário ou senha incorretos';
                    this.loading = false;
                } else {
                    if (payload.rememberme) {
                        localStorage.setItem('endurance_tk', signin.data);
                    }

                    this.$router.push({ path: '/user/overview' })
                    this.setToken(signin.data);
                    this.loading = false;
                }
            }
        }
    },
    mounted() {
        if(localStorage.getItem('endurance_tk')) {
            this.setToken(localStorage.getItem('endurance_tk'));
            this.$router.push({ path: '/user/overview' });
        }
    }
}

</script>
