<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <div class="col-md-12">
          <card>
            <h4 slot="header" class="card-title">Edit Profile</h4>
            <form>
              <div class="row">
                <div class="col-md-12">

                  <div class="form-group">
                    <label>Nome de Exibição</label>
                    <div class="input-group">
                      <input type="text" name="name" id="name" class="form-control border-input" v-model="temp_user">
                      <button class="btn btn-primary btn-fill" type="button" style="padding: 6px 16px"
                        @click="updateProfile()" :disabled="loading">
                        {{ btnText }}
                      </button>
                    </div>
                    <p class="text-success" v-if="success">{{ success }}</p>
                    <p class="text-danger" v-if="error">{{ error }}</p>
                  </div>
                </div>
              </div>
              <div class="clearfix"></div>
            </form>
          </card>
        </div>
      </div>

      <div class="row">
        <div class="col-md-12">
          <Card class="strpied-tabled-with-hover" body-classes="table-responsive">
            <template slot="header">
              <h4 class="card-title">Lista de Carros {{ user.id_carro }}</h4>
              <p class="card-category">Escolha a categoria e o carro que usará no endurance</p>
              <p class="text-danger" v-if="carError">Ops, algo deu errado... tente novamente</p>
            </template>

            <div class="category-change">
              <input type="radio" id="GT3" value="GT3" v-model="category" />
              <label for="GT3">GT3</label>
              <input type="radio" id="LMP2" value="LMP2" v-model="category" />
              <label for="LMP2">LMP2</label>
            </div>

            <div class="pilots">
              <table class="table">
                <thead>
                  <slot name="columns">
                    <tr>
                      <th>Carro</th>
                      <th>Velocidade</th>
                      <th>Aceleracao</th>
                      <th>Potencia</th>
                      <th>Peso</th>
                      <th></th>
                    </tr>
                  </slot>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in cars" :key="index"
                    :class="(user.id_carro == item.id) ? 'table-info' : ''" v-show="item.categoria == category">
                    <td>{{ (item.nome) ? item.nome : '-' }}</td>
                    <td>{{ (item.velocidade) ? (item.velocidade + ' km/h') : '-' }}</td>
                    <td>{{ (item.aceleracao) ? (item.aceleracao + 's') : '-' }}</td>
                    <td>{{ (item.potencia) ? (item.potencia + ' HP') : '-' }}</td>
                    <td>{{ (item.peso) ? (item.peso + 'kg') : '-' }}</td>
                    <td><a @click="selectCar(item)" class="btn btn-success btn-fill btn-sm"
                        :disabled="user.id_carro == item.id">Selecionar</a></td>
                  </tr>
                </tbody>
              </table>
            </div>
          </Card>
        </div>
      </div>

    </div>
  </div>
</template>
<script>
import Card from 'src/components/Cards/Card.vue'
import store from '@/store';
import { updateName, updateCar } from '@/js/update';

export default {
  components: {
    Card
  },
  computed: {
    user() {
      return store.state.user;
    },
    cars() {
      return store.state.cars;
    },
    pilots() {
      return store.state.pilots;
    }
  },
  data() {
    return {
      temp_user: null,
      loading: false,
      error: null,
      success: null,
      btnText: "Mudar nome",
      carError: false,
      category: 'GT3'
    }
  },
  methods: {
    updateName,
    updateCar,
    async updateProfile() {
      this.loading = true;
      this.error = null;
      this.success = null;
      this.btnText = "Aguarde...";

      const response = await updateName(this.temp_user);
      if (response.status != "success") {
        this.loading = false;
        this.error = 'Erro ao processar a solicitação.';
        this.btnText = "Tentar Novamente";
      } else {
        this.loading = true;
        this.success = 'Nome de exibição alterado, recarregue a página para alterar novamente.';
        this.btnText = "Nome Alterado.";
        this.user.nome_exibicao = this.temp_user;
      }
    },
    async selectCar(car) {
      this.carError = false;
      const response = await updateCar(car.id);
      if (response.status != "success") {
        this.carError = true;
      } else {
        this.user.id_carro = car.id;

        let objIndex = this.pilots.findIndex((obj => obj.nome_usuario == this.user.nome_usuario));
        this.pilots[objIndex].nome = car.nome;
        this.pilots[objIndex].categoria = car.categoria;
        this.pilots[objIndex].velocidade = car.velocidade;
        this.pilots[objIndex].aceleracao = car.aceleracao;
        this.pilots[objIndex].potencia = car.potencia;
        this.pilots[objIndex].peso = car.peso;
      }
    }
  },
  mounted() {
    this.temp_user = this.user.nome_exibicao
  }

}

</script>
