import {urlServices, token} from './variables'

const updateName = async (payload) => {
    let url =  `${urlServices}/update-name`
    if (url !== null) {
        let response;
        try {
            response = await fetch(url, {
                method: 'POST',
                body: JSON.stringify({token: token, name: payload})
            })
            if (!response.ok) {
              const error = await response.json();
              throw new Error(error.message);
            }
            const data = await response.json();
            return {
                status: 'success',
                data: data
            };
        } catch (error) {
            if(response.status === 400) {
                return {
                    status: 'error',
                    message: error.message
                };
            } else {
                console.error('Error:', error);
            }
        }
    }
}

const updateCar = async (payload) => {
    let url =  `${urlServices}/update-car`
    if (url !== null) {
        let response;
        try {
            response = await fetch(url, {
                method: 'POST',
                body: JSON.stringify({token: token, id: payload})
            })
            if (!response.ok) {
              const error = await response.json();
              throw new Error(error.message);
            }
            const data = await response.json();
            return {
                status: 'success',
                data: data
            };
        } catch (error) {
            if(response.status === 400 || response.status === 401) {
                return {
                    status: 'error',
                    message: error.message
                };
            } else {
                console.error('Error:', error);
            }
        }
    }
}

export { updateName, updateCar }

