<template>
    <div class="auth-page">
        <div class="background"></div>
        <div class="backdrop"></div>

        <div class="container">
            <div class="formulario" v-show="cadastroRealizado">
                <p class="w-100 text-center">Cadastro realizado com sucesso!</p>
                <a href="#/signin" class="btn btn-login">Voltar ao Login</a>
            </div>
            <div class="formulario" v-show="!cadastroRealizado">
                <!-- HEADER -->
                <div class="header">
                    <div class="logo">
                        <div class="logo-area">
                            <span class="big">ENDURANCE</span>
                        </div>
                    </div>
                </div>
                <!-- FORMULARIO -->
                <form method="post" action="" class="form">
                    <div class="input">
                        <i class="fa fa-user icone"></i>
                        <input type="user" name="user" placeholder="Usuario" v-model="objItem.username">
                    </div>
                    <div class="input">
                        <i class="fa fa-user icone"></i>
                        <input type="user" name="user" placeholder="Nome de Exibição" v-model="objItem.name">
                    </div>
                    <div class="input">
                        <i class="fa fa-user icone"></i>
                        <input type="user" name="user" placeholder="Código de Acesso" v-model="objItem.cod">
                    </div>
                    <div class="input">
                        <i class="fa fa-lock icone"></i>
                        <input :type="(viewPass) ? 'text' : 'password'" id="login-password" name="password"
                            placeholder="Senha" v-model="objItem.password">
                        <i id="show-password" class="fa fa-eye icone" v-show="!viewPass" @click="viewPass = true"></i>
                        <i id="show-password" class="fa fa-eye-slash icone" v-show="viewPass"
                            @click="viewPass = false"></i>
                    </div>

                    <p class="msg-error" v-show="msgError != null">* {{ msgError }}</p>

                    <p class="w-100 text-center" v-show="loading">Aguarde...</p>
                    <input value="Criar Conta" class="btn btn-login" @click="sign()" v-show="!loading">
                    <a href="#/signin" class="btn btn-registrar">Voltar ao Login</a>
                </form>
            </div>
            <p class="attrib">&copy; 2023 Filipe Daineze</p>
        </div>
    </div>
</template>
<style lang="scss">

</style>
<script>
import { token, setToken } from '@/js/variables';
import { signUp } from '@/js/auth';

export default {
    components: {
    },
    data() {
        return {
            token: token,
            objItem: {},
            cadastroRealizado: false,
            viewPass: false,
            msgError: null,
            loading: false
        };
    },
    methods: {
        setToken,
        signUp,

        async sign() {
            this.loading=true;
            let payload = JSON.parse(JSON.stringify(this.objItem));

            if(payload.username == null ||payload.name == null ||payload.cod == null ||payload.password == null) {
                this.msgError = 'Preencha todos os campos';
                this.loading=false;
            } else {
                const signup = await this.signUp(payload);
                if(signup.status == 'error') {
                    this.msgError = signup.message;
                    this.loading=false;
                } else {
                    this.cadastroRealizado = true;
                    this.loading=false;
                }
            }
        }
    },
    mounted() {
        this.setToken('teste');
    }
}

</script>
