<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <div class="col-md-6">
          <stats-card>
            <div slot="header" class="icon-warning">
              <i class="nc-icon nc-chart text-warning"></i>
            </div>
            <div slot="content">
              <p class="card-category">
                {{ (user) ? user.nome_usuario : '-' }}, {{ (user) ? user.descricao : '-' }}
              </p>
              <h4 class="card-title">{{ (user) ? user.nome_exibicao : '-' }}</h4>
            </div>
            <div slot="footer" class="float-right">
              <a href="#/user/profile">Escolher meu Carro</a> &nbsp;-&nbsp;
              <span v-if="user.id_carro"> {{(myCar) ? (myCar.categoria + " | " + myCar.nome) :
              'nenhum carro escolhido'}}</span>
            </div>
          </stats-card>
        </div>
      </div>

      <div class="row">
        <div class="col-md-12">
          <Card class="strpied-tabled-with-hover" body-classes="table-responsive">
            <template slot="header">
              <h4 class="card-title">Lista de Pilotos</h4>
              <p class="card-category">Escolha seu carro para que ele apareça nessa lista</p>
            </template>
            <div class="pilots">
              <table class="table">
                <thead>
                  <slot name="columns">
                    <tr>
                      <th>Nome</th>
                      <th>Origem</th>
                      <th>Categoria</th>
                      <th>Carro</th>
                      <th>Velocidade</th>
                      <th>Aceleracao</th>
                      <th>Potencia</th>
                      <th>Peso</th>
                    </tr>
                  </slot>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in data" :key="index">
                    <td>{{ (item.nome_exibicao) ? item.nome_exibicao : '-' }}</td>
                    <td>{{ (item.descricao) ? item.descricao : '-' }}</td>
                    <td>{{ (item.categoria) ? item.categoria : '-' }}</td>
                    <td>{{ (item.nome) ? item.nome : '-' }}</td>

                    <td>{{ (item.velocidade) ? (item.velocidade + ' km/h') : '-' }}</td>
                    <td>{{ (item.aceleracao) ? (item.aceleracao + 's') : '-' }}</td>
                    <td>{{ (item.potencia) ? (item.potencia + ' HP') : '-' }}</td>
                    <td>{{ (item.peso) ? (item.peso + 'kg') : '-' }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </Card>
        </div>

      </div>
    </div>
  </div>
</template>
<script>
import LTable from 'src/components/Table.vue';
import ChartCard from 'src/components/Cards/ChartCard.vue';
import StatsCard from 'src/components/Cards/StatsCard.vue';
import Card from 'src/components/Cards/Card.vue';
import store from '@/store';

export default {
  components: {
    LTable,
    ChartCard,
    StatsCard,
    Card
  },
  computed: {
    user() {
      return store.state.user;
    },
    cars() {
      return store.state.cars;
    },
    pilots() {
      return store.state.pilots;
    }
  },
  data() {
    return {
      pieChart: {
        data: {
          labels: ['40%', '60%'],
          series: [40, 60]
        }
      },
      data: [],
      myCar: null,
      carList: [],
    }
  },
  methods: {
    getCar(id) {
      if (id) {
        let carList = JSON.parse(JSON.stringify(this.cars));
        if (carList != undefined) {
          var result = carList.filter(obj => {
            return obj.id === id
          })
          return result;
        }
        return [];
      } else {
        return [];
      }
    }
  },
  async mounted() {
    if (this.user.id_carro && this.cars) {
      this.myCar = this.getCar(this.user.id_carro)[0];
    }
    if (this.pilots) {
      this.data = this.pilots;
    }
  },
  watch: {
    user() {
      if (this.user.id_carro && this.cars) {
        this.myCar = this.getCar(this.user.id_carro)[0];
      }
    },
    cars() {
      if (this.user.id_carro && this.cars) {
        this.myCar = this.getCar(this.user.id_carro)[0];
      }
    },
    pilots() {
      if (this.pilots) {
        this.data = this.pilots;
      }
    }
  }
}
</script>
