<script>
import { token, setToken } from '@/js/variables';
import { findUser, findAllCars, findAllPilots } from '@/js/find';
import store from '@/store';

import TopNavbar from './TopNavbar.vue'
import DashboardContent from './Content.vue'
export default {
  components: {
    TopNavbar,
    DashboardContent
  },
  computed: {
    user() {
      return store.state.user;
    }
  },
  data() {
    return {
    };
  },
  methods: {
    setUser(user) {
      store.commit('setUser', user)
    },
    signOut() {
      localStorage.removeItem('endurance_tk');
      this.$router.push({ path: '/signin' });
    },
    findUser,
    findAllCars,
    findAllPilots,
    setToken
  },
  async mounted() {
    let tk = token;
    if (tk == undefined && localStorage.getItem('endurance_tk')) {
      this.setToken(localStorage.getItem('endurance_tk'));
    }
    const response = await this.findUser();
    if (response.status != "success") {
      this.signOut();
    } else {
      this.setUser(response.data);

      const carList = await this.findAllCars();
      store.commit('setCars', carList.data)

      const pilotList = await this.findAllPilots();
      store.commit('setPilots', pilotList.data)
    }
  }
}

</script>
