import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const state = {
    user: {},
    cars: [],
    pilots: []
}
const mutations = {
    setUser(state, user) {
        state.user = user
    },
    setCars(state, cars) {
        state.cars = cars
    },
    setPilots(state, pilots) {
        state.pilots = pilots
    }
}

export default new Vuex.Store({
    state,
    mutations
  })